import './App.css';
import Header from './Header';
import Home from './Home/Home';
import Stadium from './Stadium/Stadium';
import Store from './Store/Store';

import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";

function App() {
  return (
    <div className="app">
    <Router>
      <Routes>
          <Route path={process.env.PUBLIC_URL + "/"} element={<Home />}></Route>
          <Route exact path={process.env.PUBLIC_URL + "/stadium"} element={<Stadium />}></Route>
          <Route exact path={process.env.PUBLIC_URL + "/store"} element={<Store />}></Route>
      </Routes>
    </Router>

    </div>
  );
}

export default App;
