import React from 'react'
import './Channel.css'

const Channel = ({avatar, name, isOnline}) => {
  return (
    <div className='channel'>
      <div className='channel__details'>
          <img src={avatar} alt="avatar" />
          <p>{name}</p>
      </div>
      <p>{isOnline}</p>
    </div>
  )
}

export default Channel
