import React from 'react'
import {Avatar} from '@material-ui/core'
import './Header.css'
import logo from "./img/Onsta_logo.png"

const Header = () => {
  return (
    <div className="header">
      <div className="header__left">
        <img className="header__leftLogo" src={logo} alt="Onsta logo" />
        <h2>E</h2>
        <h2></h2>
        <div className="header__verticalLine"></div>
        <h2></h2>
        <h2>2</h2>
        <i className="fas fa-ellipsis-h"></i>
      </div>

      <div className="header__center">
        <input type="text" placeholder=""/>
        <div className="header__centerLogoContainer">
            <i className="fas fa-search"></i>
        </div>
      </div>

      <div className="header__right">
        <div className="header__rightContainer">
            <i className="headerIcon fas fa-inbox"></i>
            <i className="headerIcon fas fa-comment-alt"></i>

            <div className="header__ticket">
              <i class="fa-solid fa-ticket-simple"></i>
                <h4> </h4>
            </div>

            <div className="myProfileIcon">
              <Avatar>
                  (  )
              </Avatar>
            </div>
        </div>
      </div>

    </div>
  )
}

export default Header
